"use client";

import { useState, useEffect } from "react";
import {
  Card,
  Button,
  Input,
  message,
  Table,
  Popconfirm,
  Tag,
  Modal,
  Form,
  Select,
  Upload,
  Spin,
} from "antd";
import {
  Plus,
  Search,
  Edit,
  Trash2,
  Eye,
  Upload as UploadIcon,
} from "lucide-react";
import {
  getAllActivities,
  createActivity,
  updateActivity,
  deleteActivity,
} from "@/services/activity";
import { getAllDepartments } from "@/services/department";
import { getAllTypeActivities } from "@/services/typeActivity";

const { Option } = Select;

export default function TeacherEventPage() {
  const [searchQuery, setSearchQuery] = useState("");
  const [userDepartment, setUserDepartment] = useState(null);
  const [userDepartmentId, setUserDepartmentId] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isViewModalVisible, setIsViewModalVisible] = useState(false);
  const [editingEvent, setEditingEvent] = useState(null);
  const [viewingEvent, setViewingEvent] = useState(null);
  const [form] = Form.useForm();
  const [events, setEvents] = useState([]);
  const [departments, setDepartments] = useState([]);
  const [typeActivities, setTypeActivities] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    // อ่านข้อมูลภาควิชาของครูจาก localStorage
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      try {
        const userData = JSON.parse(storedUser);

        // Get department info from localStorage
        if (userData.departmentName) {
          setUserDepartment(userData.departmentName);
          setUserDepartmentId(userData.departmentId);
        }
      } catch (error) {
        console.error("Error parsing user data:", error);
        message.error("ไม่สามารถโหลดข้อมูลผู้ใช้ได้");
      }
    }
  }, []);

  // Fetch data from API
  const fetchData = async () => {
    setLoading(true);
    try {
      const [activitiesData, departmentsData, typeActivitiesData] =
        await Promise.all([
          getAllActivities(),
          getAllDepartments(),
          getAllTypeActivities(),
        ]);

      setEvents(activitiesData);
      setDepartments(departmentsData);
      setTypeActivities(typeActivitiesData);
    } catch (error) {
      console.error("Error fetching data:", error);
      message.error("ไม่สามารถโหลดข้อมูลได้");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (userDepartmentId || userDepartment) {
      fetchData();
    }
  }, [userDepartmentId, userDepartment]);

  const majors = ["TCT", "CED"];
  const yearLevels = ["1", "2", "3", "4"];

  // Filter events based on teacher's department, search, and category
  const getFilteredEvents = () => {
    let filtered = [...events];

    // Filter by teacher's department automatically
    filtered = filtered.filter((event) => {
      if (event.department?.id && userDepartmentId) {
        return event.department.id === userDepartmentId;
      }
      if (event.department?.name && userDepartment) {
        return event.department.name === userDepartment;
      }
      return false;
    });

    // Apply search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter((event) =>
        event.name?.toLowerCase().includes(query)
      );
    }

    // Apply category filter
    if (selectedCategory) {
      filtered = filtered.filter(
        (event) => event.typeActivity?.name === selectedCategory
      );
    }

    return filtered;
  };

  // Calculate summary statistics
  const getSummary = () => {
    const filtered = getFilteredEvents();
    const totalActivities = filtered.length;
    const totalHours = filtered.reduce(
      (sum, event) => sum + (event.hour || 0),
      0
    );
    return { totalActivities, totalHours };
  };

  // Open modal for create
  const handleCreate = () => {
    setEditingEvent(null);
    form.resetFields();
    setIsModalVisible(true);
  };

  // Open modal for edit
  const handleEdit = (record) => {
    setEditingEvent(record);
    form.setFieldsValue(record);
    setIsModalVisible(true);
  };

  // View event details
  const handleView = (record) => {
    setViewingEvent(record);
    setIsViewModalVisible(true);
  };

  // Delete event
  const handleDelete = async (record) => {
    try {
      await deleteActivity(record.id);
      message.success(`ลบกิจกรรม "${record.name}" สำเร็จ`);
      fetchData();
    } catch (error) {
      console.error("Error deleting activity:", error);
      message.error("ไม่สามารถลบกิจกรรมได้");
    }
  };

  // Submit form (create or update)
  const handleSubmit = async (values) => {
    setLoading(true);
    try {
      // เตรียมข้อมูลสำหรับ API
      const formData = {
        name: values.activityName,
        description: values.description,
        departmentId: userDepartmentId,
        typeActivityId: values.category,
        startDate: new Date(values.startDate).toISOString(),
        endDate: new Date(values.endDate).toISOString(),
        address: values.location,
        hour: parseInt(values.activityHours),
        maxPeopleCount: parseInt(values.maxStudents),
        level: Array.isArray(values.yearLevels)
          ? values.yearLevels.join(",")
          : values.yearLevels,
        date: new Date(values.activityDeadline).toISOString(),
      };

      if (editingEvent) {
        await updateActivity(editingEvent.id, formData);
        message.success("แก้ไขกิจกรรมสำเร็จ");
      } else {
        await createActivity(formData);
        message.success("เพิ่มกิจกรรมสำเร็จ");
      }

      setIsModalVisible(false);
      form.resetFields();
      fetchData();
    } catch (error) {
      console.error("Error submitting activity:", error);
      message.error("ไม่สามารถบันทึกกิจกรรมได้");
    } finally {
      setLoading(false);
    }
  };

  const columns = [
    {
      title: "No.",
      dataIndex: "index",
      key: "index",
      width: 80,
      align: "center",
      render: (_, __, index) => index + 1,
    },
    {
      title: "Activity",
      dataIndex: "name",
      key: "name",
      ellipsis: true,
    },
    {
      title: "Department",
      dataIndex: "department",
      key: "department",
      width: 200,
      render: (department) => department?.name || "N/A",
    },
    {
      title: "Category",
      dataIndex: "typeActivity",
      key: "typeActivity",
      width: 150,
      align: "center",
      render: (typeActivity) => (
        <Tag
          color="#0A894C"
          style={{
            borderRadius: 8,
            fontWeight: 500,
            fontSize: "11px",
          }}
        >
          {typeActivity?.name || "N/A"}
        </Tag>
      ),
    },
    {
      title: "Students",
      dataIndex: "peopleCount",
      key: "peopleCount",
      width: 150,
      align: "center",
      render: (count, record) => {
        const maxCount = record.maxPeopleCount || 0;
        const currentCount = count || 0;
        const percentage = maxCount > 0 ? (currentCount / maxCount) * 100 : 0;
        const color =
          percentage >= 80
            ? "#f5222d"
            : percentage >= 50
            ? "#fa8c16"
            : "#0A894C";

        return (
          <span
            style={{
              color: color,
              fontWeight: "600",
              fontSize: "14px",
            }}
          >
            {currentCount}/{maxCount}
          </span>
        );
      },
    },
    {
      title: "Actions",
      key: "action",
      width: 150,
      align: "center",
      render: (_, record) => (
        <div className="flex gap-2 justify-center">
          <Button
            type="text"
            icon={<Eye size={18} />}
            onClick={() => handleView(record)}
            style={{ color: "#0A894C" }}
            title="View Details"
          />
          <Button
            type="text"
            icon={<Edit size={18} />}
            onClick={() => handleEdit(record)}
            style={{ color: "#1890ff" }}
            title="Edit"
          />
          <Popconfirm
            title="Confirm Delete"
            description="Are you sure you want to delete this event?"
            onConfirm={() => handleDelete(record)}
            okText="Confirm"
            cancelText="Cancel"
            okButtonProps={{
              style: { backgroundColor: "#0A894C", borderColor: "#0A894C" },
            }}
          >
            <Button
              type="text"
              icon={<Trash2 size={18} />}
              danger
              title="Delete"
            />
          </Popconfirm>
        </div>
      ),
    },
  ];

  return (
    <div>
      {/* Department Banner */}
      <Card
        className="mb-4 shadow-sm"
        style={{
          background: "linear-gradient(135deg, #0A894C 0%, #0ea258 100%)",
          border: "none",
        }}
      >
        <div className="text-white">
          <h2 className="text-xl font-bold mb-1">ภาควิชา {userDepartment}</h2>
          <p className="text-sm opacity-90">จัดการกิจกรรมของภาควิชา</p>
        </div>
      </Card>
      <br />
      {/* Header with Search and Create Button */}
      <div
        className="p-4 rounded-lg mb-4"
        style={{
          background: "#ffffff",
          border: "1px solid #e8f5e9",
          boxShadow: "0 2px 8px rgba(10, 137, 76, 0.08)",
        }}
      >
        <div className="flex flex-wrap items-center justify-between gap-4 mb-4">
          <Input
            placeholder="Search event name..."
            prefix={<Search size={16} style={{ color: "#0A894C" }} />}
            onChange={(e) => setSearchQuery(e.target.value)}
            size="large"
            style={{
              maxWidth: 400,
              borderColor: "#0A894C",
            }}
          />
          <Button
            type="primary"
            icon={<Plus size={18} />}
            onClick={handleCreate}
            size="large"
            style={{
              backgroundColor: "#0A894C",
              borderColor: "#0A894C",
            }}
          >
            Create Event
          </Button>
        </div>

        {/* Filter Section */}
        <div className="flex flex-wrap items-center gap-4">
          <div className="flex items-center gap-2">
            <label className="text-sm font-medium text-gray-700">
              Filter by Category:
            </label>
            <Select
              placeholder="All Categories"
              allowClear
              value={selectedCategory}
              onChange={setSelectedCategory}
              style={{ minWidth: 280 }}
              size="middle"
            >
              {typeActivities.map((category) => (
                <Option key={category.id} value={category.name}>
                  {category.name}
                </Option>
              ))}
            </Select>
          </div>

          {selectedCategory && (
            <Button
              size="small"
              onClick={() => setSelectedCategory(null)}
              style={{ color: "#0A894C", borderColor: "#0A894C" }}
            >
              Clear Filter
            </Button>
          )}
        </div>
      </div>

      {/* Summary Section */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
        <Card
          style={{
            borderRadius: 8,
            border: "1px solid #e8f5e9",
            boxShadow: "0 2px 8px rgba(10, 137, 76, 0.08)",
            background: "linear-gradient(135deg, #0A894C 0%, #0ea258 100%)",
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="text-white text-sm mb-1 opacity-90">
                Total Activities
              </p>
              <h2 className="text-white text-3xl font-bold">
                {getSummary().totalActivities}
              </h2>
            </div>
            <div
              className="p-4 rounded-full"
              style={{ backgroundColor: "rgba(255, 255, 255, 0.2)" }}
            >
              <Search size={32} className="text-white" />
            </div>
          </div>
        </Card>

        <Card
          style={{
            borderRadius: 8,
            border: "1px solid #e8f5e9",
            boxShadow: "0 2px 8px rgba(10, 137, 76, 0.08)",
            background: "linear-gradient(135deg, #1890ff 0%, #40a9ff 100%)",
          }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="text-white text-sm mb-1 opacity-90">
                Total Activity Hours
              </p>
              <h2 className="text-white text-3xl font-bold">
                {getSummary().totalHours}
              </h2>
            </div>
            <div
              className="p-4 rounded-full"
              style={{ backgroundColor: "rgba(255, 255, 255, 0.2)" }}
            >
              <Plus size={32} className="text-white" />
            </div>
          </div>
        </Card>
      </div>

      {/* Events Table */}
      <Card
        style={{
          borderRadius: 8,
          border: "1px solid #e8f5e9",
          boxShadow: "0 2px 8px rgba(10, 137, 76, 0.08)",
        }}
      >
        <Spin spinning={loading}>
          <Table
            columns={columns}
            dataSource={getFilteredEvents()}
            rowKey="id"
            pagination={{
              pageSize: 10,
              showSizeChanger: true,
              showTotal: (total) => `Total ${total} items`,
            }}
            scroll={{ x: 800 }}
          />
        </Spin>
      </Card>

      {/* Create/Edit Modal */}
      <Modal
        title={
          <h3 className="text-lg font-semibold" style={{ color: "#0A894C" }}>
            {editingEvent ? "Edit Event" : "Create New Event"}
          </h3>
        }
        open={isModalVisible}
        onCancel={() => {
          setIsModalVisible(false);
          form.resetFields();
        }}
        onOk={() => form.submit()}
        okText={editingEvent ? "Save" : "Create"}
        cancelText="Cancel"
        width={800}
        okButtonProps={{
          style: { backgroundColor: "#0A894C", borderColor: "#0A894C" },
        }}
        styles={{
          header: {
            borderBottom: "2px solid #0A894C",
            paddingBottom: 16,
          },
        }}
      >
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSubmit}
          className="mt-4"
        >
          <Form.Item
            label="ชื่อกิจกรรม (Activity Name)"
            name="activityName"
            rules={[{ required: true, message: "Please enter activity name" }]}
          >
            <Input placeholder="Activity Name" />
          </Form.Item>

          <Form.Item
            label="หมวดกิจกรรม (Category)"
            name="category"
            rules={[{ required: true, message: "Please select category" }]}
          >
            <Select placeholder="Select category" style={{ width: "100%" }}>
              {typeActivities.map((category) => (
                <Option key={category.id} value={category.id}>
                  {category.name}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item
            label="รายละเอียด (Description)"
            name="description"
            rules={[{ required: true, message: "Please enter description" }]}
          >
            <Input.TextArea rows={4} placeholder="Activity Description" />
          </Form.Item>

          <Form.Item
            label="ระดับชั้นปี (Year Level)"
            name="yearLevels"
            rules={[
              {
                required: true,
                message: "Please select at least one year level",
              },
            ]}
          >
            <Select
              mode="multiple"
              placeholder="Select year levels"
              style={{ width: "100%" }}
            >
              {yearLevels.map((year) => (
                <Option key={year} value={year}>
                  Year {year}
                </Option>
              ))}
            </Select>
          </Form.Item>

          <div className="grid grid-cols-2 gap-4">
            <Form.Item
              label="ระยะเวลาที่จัด - เริ่ม (Start Date)"
              name="startDate"
              rules={[{ required: true, message: "Please select start date" }]}
            >
              <Input type="date" />
            </Form.Item>

            <Form.Item
              label="ระยะเวลาที่จัด - สิ้นสุด (End Date)"
              name="endDate"
              rules={[{ required: true, message: "Please select end date" }]}
            >
              <Input type="date" />
            </Form.Item>
          </div>

          <Form.Item
            label="สถานที่ (Location)"
            name="location"
            rules={[{ required: true, message: "Please enter location" }]}
          >
            <Input placeholder="Event Location" />
          </Form.Item>

          <div className="grid grid-cols-2 gap-4">
            <Form.Item
              label="จำนวนชั่วโมงกิจกรรม (Activity Hours)"
              name="activityHours"
              rules={[
                { required: true, message: "Please enter activity hours" },
              ]}
            >
              <Input type="number" placeholder="Hours" min={1} />
            </Form.Item>

            <Form.Item
              label="จำนวนนักศึกษาสูงสุด (Max Students)"
              name="maxStudents"
              rules={[{ required: true, message: "Please enter max students" }]}
            >
              <Input type="number" placeholder="Max Students" min={1} />
            </Form.Item>
          </div>

          <Form.Item label="ภาพประกอบ (Image)" name="imageUrl">
            <Upload
              listType="picture-card"
              maxCount={1}
              beforeUpload={() => false}
              accept="image/*"
            >
              <div>
                <UploadIcon size={20} />
                <div style={{ marginTop: 8 }}>Upload</div>
              </div>
            </Upload>
          </Form.Item>

          <Form.Item
            label="กำหนดเวลากิจกรรม (Activity Deadline)"
            name="activityDeadline"
            rules={[{ required: true, message: "Please select deadline" }]}
          >
            <Input type="date" />
          </Form.Item>

          <Form.Item label="หมายเหตุ (Remarks)" name="remarks">
            <Input.TextArea
              rows={2}
              placeholder="Additional notes or remarks"
            />
          </Form.Item>
        </Form>
      </Modal>

      {/* View Details Modal */}
      <Modal
        title={
          <h3 className="text-lg font-semibold" style={{ color: "#0A894C" }}>
            Event Details
          </h3>
        }
        open={isViewModalVisible}
        onCancel={() => setIsViewModalVisible(false)}
        footer={[
          <Button key="close" onClick={() => setIsViewModalVisible(false)}>
            Close
          </Button>,
        ]}
        width={700}
        styles={{
          header: {
            borderBottom: "2px solid #0A894C",
            paddingBottom: 16,
          },
        }}
      >
        {viewingEvent && (
          <div className="mt-4 space-y-3">
            <div>
              <label className="font-semibold text-gray-700">
                ชื่อกิจกรรม (Activity Name):
              </label>
              <p className="text-gray-600 mt-1">{viewingEvent.activityName}</p>
            </div>
            <div>
              <label className="font-semibold text-gray-700">
                ภาควิชา (Department):
              </label>
              <div className="mt-1 flex flex-wrap gap-2">
                {viewingEvent.departments?.map((dept, idx) => (
                  <Tag key={idx} color="blue">
                    {dept}
                  </Tag>
                ))}
              </div>
            </div>
            <div>
              <label className="font-semibold text-gray-700">
                สาขาวิชา (Major):
              </label>
              <div className="mt-1 flex flex-wrap gap-2">
                {viewingEvent.majors?.map((major, idx) => (
                  <Tag key={idx} color="#0A894C">
                    {major}
                  </Tag>
                ))}
              </div>
            </div>
            <div>
              <label className="font-semibold text-gray-700">
                ระดับชั้นปี (Year Level):
              </label>
              <div className="mt-1 flex flex-wrap gap-2">
                {viewingEvent.yearLevels?.map((year, idx) => (
                  <Tag key={idx} color="purple">
                    Year {year}
                  </Tag>
                ))}
              </div>
            </div>
            <div>
              <label className="font-semibold text-gray-700">
                รายละเอียด (Description):
              </label>
              <p className="text-gray-600 mt-1">{viewingEvent.description}</p>
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="font-semibold text-gray-700">
                  ระยะเวลาที่จัด (Duration):
                </label>
                <p className="text-gray-600 mt-1">
                  {viewingEvent.startDate} to {viewingEvent.endDate}
                </p>
              </div>
              <div>
                <label className="font-semibold text-gray-700">
                  สถานที่ (Location):
                </label>
                <p className="text-gray-600 mt-1">{viewingEvent.location}</p>
              </div>
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="font-semibold text-gray-700">
                  จำนวนชั่วโมง (Activity Hours):
                </label>
                <p className="text-gray-600 mt-1">
                  {viewingEvent.activityHours} hours
                </p>
              </div>
              <div>
                <label className="font-semibold text-gray-700">
                  นักศึกษา (Students):
                </label>
                <p className="text-gray-600 mt-1">
                  {viewingEvent.studentCount}/{viewingEvent.maxStudents}{" "}
                  students
                </p>
              </div>
            </div>
            <div>
              <label className="font-semibold text-gray-700">
                กำหนดเวลา (Deadline):
              </label>
              <p className="text-gray-600 mt-1">
                {viewingEvent.activityDeadline}
              </p>
            </div>
            {viewingEvent.remarks && (
              <div>
                <label className="font-semibold text-gray-700">
                  หมายเหตุ (Remarks):
                </label>
                <p className="text-gray-600 mt-1">{viewingEvent.remarks}</p>
              </div>
            )}
          </div>
        )}
      </Modal>
    </div>
  );
}
